import React, {useState, useEffect} from 'react';
import './Result.css';
import './Search.css';
import BookResult from './BookResult';

function Result() {
    
    const [data, setData] = useState("");
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");

    //const [search, setSearch] = useState("");
    const [result, setResult] = useState([]);
    
    async function getData(req) {
        setLoading(true);
        let bookTitle = req.split(" ").join("+")
        fetch(`https://openlibrary.org/search.json?title=${bookTitle}`)
            .then((response) => response.json())
            .then((data) => {setData(data); setResult(data.docs)})
            .then(() => setLoading())
            .catch(setError);
    }

    /*useEffect(() => {
        setLoading(true);
        fetch("http://openlibrary.org/search.json?author=tolkien")
            .then((response) => response.json())
            .then((data) => setData(data))
            .then(() => setLoading())
            .catch(setError);

    }, []);*/

    if (loading) {
        return <h2 className="animate">Receiving data...</h2>
    }

    if (error) {
        return <pre>{JSON.stringify(error, null, 2)}</pre>
    }

    let resArray = data.docs;
    
    /*const searchHandler = (search) => {
        setSearch(search);

        if(search !== "") {
            const newResult = resArray.filter((book) => {
                return Object.values(book.title).join("").toLowerCase().includes(search.toLowerCase())
            });
            setResult(newResult);
        } else {
            setResult(resArray);
        }
    }*/

    function handleSearch() {
        let searchBar = document.getElementById("search-bar");
        if (searchBar.value) {
            getData(searchBar.value)
        }
    }

    console.log(result);

    return (
        <div className="container">
            <div className="search"> 
                <input type="text" id="search-bar" placeholder="Введите название книги" /*onChange={handleSearch}*/></input>
                <a id="search-button" onClick={handleSearch}>search</a>
            </div>
            {!result ?
            /*<div className="result">
                {resArray.map((elem, idx) => {
                    return <div className="book" key={idx}>{elem.title}</div>
                })}
            </div>*/
            null
            :
            <BookResult res={result}/>
            }
        </div>
    )

    
    
}

export default Result;
